'use strict';

const burgerIcon = document.querySelector('.burgerIcon');
const burgerMenu = document.querySelector('.burgerMenu');

burgerIcon.addEventListener('click', () => {
  burgerMenu.classList.toggle('open');
  burgerIcon.classList.toggle('close');
});

const burgerMenuBody = document.querySelector(".burgerMenuBody")


burgerMenuBody.addEventListener("click", (event) => {
  event.preventDefault();
  const target = event.target


  if (target.nodeName !== "A" ) {
    return;
  }

  burgerMenu.classList.toggle('open');
  burgerIcon.classList.toggle('close');

  const idTo = target.dataset.to;
  const targetElement = document.getElementById(idTo);

  if (targetElement) {
    targetElement.scrollIntoView({ behavior: 'smooth' });
  }

})
